
.general {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    align-items: center;
    width: 88%;
    margin: 30px 6% 0 6%;
    gap: 40px;
    justify-content: space-evenly;
    color: rgb(163, 163, 163);
    text-align: center;
}

.general p {
    font-size: large;
    grid-column: 2/3;
}
.filter {
    color: rgb(159, 159, 159);
    font-size: large;
    text-align: left;
    margin: 0 0 0 3rem;
    width: 50%;
}
.filter select{
    width: 50%;
}
label {
    margin-right: 10px;
    font-size: large;
}
select {
    margin-top: 20px;
    background-color: rgb(200, 200, 200);
    border-radius: 5px;
    font-size: large;
}
select option {
    margin-top: 20px;
    background-color: rgb(206, 193, 212);
    border-radius: 5px;
    font-size: large;
}
.loading{
    cursor: wait;
}
/* 
*
*
CARTEl
*
*
 */
.cartel {
    background-color: rgb(6, 0, 5);
    border-radius: 10px;
    padding: 20px 1px 5px 1px;
    width: 100%;
    height: 100%;
}
.cartel audio {
    padding-top: 15px;
}
.cartel img {
    border-radius: 10px;
    max-width: 300px;
    transition: 0.5s;
    opacity: 50%;
    padding: 10px;
    background-color: rgba(255, 255, 255, 0);
}
.cartel img:hover {
    opacity: 100%;
    transform: scale(1.08);
    background-color: rgba(163, 163, 163, 0);
}

.cartel .title {
    margin: 0;
    padding: 0;
    text-transform: capitalize;
}
.cartel .artist {
    margin: 0;
    padding: 0;
    color: rgb(86, 0, 144);
    text-transform: capitalize;
}
.cartel a {
    color: rgba(2, 206, 2, 0.815);
    text-decoration: none;
}
.cartel p {
    color: rgba(96, 96, 96, 0.815);
    text-decoration: none;
}
/* 
*
*
NAV
*
*
 */
.nav {
    background-color: rgb(0, 0, 0);
    height: 60px;
    width: 100%;
    text-align: right;
}
.nav .user {
    background-color: rgba(4, 32, 240, 0.622);
    border-radius: 6px;
    border-color: black;
    color: rgb(227, 227, 227);
    margin: 15px 19px 15px 0;
    font-size: large;
    transition: 1s;
}
.nav .user:hover {
    background-color: rgb(5, 0, 164);
    box-shadow: 0px 0px 100px 20px rgb(255, 255, 255);
}
.nav .logout {
    background-color: rgba(227, 36, 36, 0.606);
    border-radius: 6px;
    color: rgb(225, 225, 225);
    margin: 15px 19px 15px 0;
    font-size: large;
    transition: 1s;
}
.nav .logout:hover {
    cursor: no-drop;
    background-color: rgb(255, 0, 0);
    box-shadow: 0px 0px 100px 20px rgb(248, 15, 15);
}
.nav .fetchbutton {
    background-color: rgba(10, 210, 27, 0.728);
    border-radius: 6px;
    color: rgb(255, 255, 255);
    margin: 15px 19px 15px 0;
    font-size: large;
    transition: 1s;
}
.nav .fetchbutton:hover {
    background-color: green;
    box-shadow: 0px 0px 100px 20px rgb(255, 2, 255);
}
.nav p {
    color: white;
    background-color: rgba(127, 255, 212, 055, 255);
    margin: 14px 0 0 10px;
    float: left;
    font-family: 'Secular One', sans-serif;
    font-size: 140%;
    background: -webkit-linear-gradient(#00b3ff, #00060900);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-transform: capitalize;
    transition: 1s;
}
.nav p:hover {
    cursor: default;
    background-color:  rgb(255, 2, 255);
}
/* 
*
*
FORM
*
*
 */
.myform {
    margin: 10% 30% 10% 30%;
    min-width: 260px;
    background-color: rgb(56, 4, 104);
    font-size: large;
    color: rgb(245, 245, 245);
    border-radius: 10px;
    padding: 15px;
    box-shadow: 0px 0px 20px 1px rgba(255, 255, 255, 0.302);
    transition: 1s;
}
.myform input {
    width: 50%;
    margin: 5px 5px 5px 5px;
    font-size: large;
}
.myform button {
    background-color: rgba(58, 10, 10, 0.545);
    border-radius: 6px;
    color: rgb(243, 243, 243);
    margin: 15px 0 15px 0;
    font-size: large;
    transition: 1s;
}
.myform button:hover {
    background-color: rgba(255, 120, 18, 0.733);
    color: black;
}
.myform p {
    color: red;
    margin: 0;
    padding: 0;
}

@media screen and (max-width: 1070px){
    .cartel img {
        max-width: 250px;
    }
    .general{
        grid-template-columns: 1fr 1fr;
    }
    .general p {
        font-size: large;
        grid-column: 1/3;
    }
}
@media screen and (max-width: 651px){
    .general{
        grid-template-columns: 1fr ;
        width: 80%;
        justify-content: center;
        margin-left: 14%;
    }
    .cartel img {
        max-width: 80%;
    }
    .nav p{
        justify-content: center;
    }
    .nav button{
        justify-content: center;
    }
    .nav{
        height: 40%;
    }
    .myform {
        margin: 15% 15% 5% 15%;}
}
@media screen and (max-width: 370px){
    .myform {
        margin: 3px;
    }
    .nav p{
        float: right;
    }
    .general{
        width: 80%;
        justify-content: center;
        margin-left: 14%;
    }
    .cartel{
        width: 100%;
        justify-content: center;
    }
    .cartel img{
        width: 90%;
    }
    .cartel audio{
        width: 70%;
    }
}
